"use client";

import React, { useState, useEffect, useRef } from "react";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { CyanAI } from "./AIOrchestrator";

interface Message {
  id: string;
  content: string;
  sender: "user" | "cyan";
  timestamp: Date;
  emotion?: "happy" | "excited" | "curious" | "thoughtful" | "concerned";
}

interface ChatInterfaceProps {
  cyanAI: CyanAI;
}

const ChatInterface: React.FC<ChatInterfaceProps> = ({ cyanAI }) => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [inputValue, setInputValue] = useState("");
  const [isTyping, setIsTyping] = useState(false);
  const [cyanEmotion, setCyanEmotion] = useState<Message["emotion"]>("happy");
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when new messages arrive
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  // Initialize with a greeting
  useEffect(() => {
    const greeting: Message = {
      id: `msg_${Date.now()}`,
      content: "Hi there! I'm Cyan! I'm so excited to chat with you! What would you like to talk about today?",
      sender: "cyan",
      timestamp: new Date(),
      emotion: "excited"
    };
    setMessages([greeting]);
  }, []);

  const sendMessage = async () => {
    if (!inputValue.trim()) return;

    // Add user message
    const userMessage: Message = {
      id: `msg_${Date.now()}_user`,
      content: inputValue,
      sender: "user",
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputValue("");
    setIsTyping(true);

    // Simulate typing delay
    setTimeout(async () => {
      try {
        // Get response from Cyan AI
        const response = await cyanAI.processInput(inputValue);
        
        // Determine emotion based on response content
        let emotion: Message["emotion"] = "happy";
        if (response.includes("exciting") || response.includes("wow") || response.includes("amazing")) {
          emotion = "excited";
        } else if (response.includes("think") || response.includes("hmm")) {
          emotion = "thoughtful";
        } else if (response.includes("curious") || response.includes("wonder")) {
          emotion = "curious";
        } else if (response.includes("sorry") || response.includes("concern")) {
          emotion = "concerned";
        }

        setCyanEmotion(emotion);

        const cyanMessage: Message = {
          id: `msg_${Date.now()}_cyan`,
          content: response,
          sender: "cyan",
          timestamp: new Date(),
          emotion
        };

        setMessages(prev => [...prev, cyanMessage]);
      } catch (error) {
        console.error("Error getting AI response:", error);
        const errorMessage: Message = {
          id: `msg_${Date.now()}_error`,
          content: "Oops! Something went wrong with my thinking process. Can you try asking me again?",
          sender: "cyan",
          timestamp: new Date(),
          emotion: "concerned"
        };
        setMessages(prev => [...prev, errorMessage]);
      } finally {
        setIsTyping(false);
      }
    }, 1000 + Math.random() * 2000); // Random delay between 1-3 seconds
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const getEmotionColor = (emotion?: Message["emotion"]) => {
    switch (emotion) {
      case "excited": return "text-yellow-400";
      case "curious": return "text-purple-400";
      case "thoughtful": return "text-blue-400";
      case "concerned": return "text-red-400";
      default: return "text-cyan-400";
    }
  };

  const getEmotionEmoji = (emotion?: Message["emotion"]) => {
    switch (emotion) {
      case "excited": return "âœ¨";
      case "curious": return "ðŸ¤”";
      case "thoughtful": return "ðŸ’­";
      case "concerned": return "ðŸ˜Ÿ";
      default: return "ðŸ˜Š";
    }
  };

  return (
    <div className="flex flex-col h-full bg-gray-900 rounded-lg border border-cyan-500">
      {/* Chat Header */}
      <div className="p-4 border-b border-gray-700 bg-gradient-to-r from-cyan-900 to-blue-900 rounded-t-lg">
        <div className="flex items-center space-x-3">
          <div className="w-10 h-10 bg-cyan-400 rounded-full flex items-center justify-center">
            <span className="text-lg">{getEmotionEmoji(cyanEmotion)}</span>
          </div>
          <div>
            <h3 className="font-semibold text-white">Cyan</h3>
            <p className={`text-sm ${getEmotionColor(cyanEmotion)}`}>
              {isTyping ? "Typing..." : `Feeling ${cyanEmotion || "happy"}`}
            </p>
          </div>
        </div>
      </div>

      {/* Messages Area */}
      <div className="flex-1 p-4 overflow-y-auto space-y-4 min-h-0">
        {messages.map((message) => (
          <div
            key={message.id}
            className={`flex ${message.sender === "user" ? "justify-end" : "justify-start"}`}
          >
            <div
              className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg ${
                message.sender === "user"
                  ? "bg-blue-600 text-white"
                  : "bg-gray-800 text-white border border-cyan-500"
              }`}
            >
              {message.sender === "cyan" && (
                <div className="flex items-center space-x-2 mb-1">
                  <span className="text-xs">{getEmotionEmoji(message.emotion)}</span>
                  <span className={`text-xs ${getEmotionColor(message.emotion)}`}>
                    Cyan
                  </span>
                </div>
              )}
              <p className="text-sm">{message.content}</p>
              <p className="text-xs opacity-70 mt-1">
                {message.timestamp.toLocaleTimeString()}
              </p>
            </div>
          </div>
        ))}
        
        {/* Typing Indicator */}
        {isTyping && (
          <div className="flex justify-start">
            <div className="bg-gray-800 border border-cyan-500 px-4 py-2 rounded-lg">
              <div className="flex items-center space-x-2">
                <span className="text-cyan-400">Cyan is thinking</span>
                <div className="flex space-x-1">
                  <div className="w-2 h-2 bg-cyan-400 rounded-full animate-bounce"></div>
                  <div className="w-2 h-2 bg-cyan-400 rounded-full animate-bounce" style={{ animationDelay: "0.1s" }}></div>
                  <div className="w-2 h-2 bg-cyan-400 rounded-full animate-bounce" style={{ animationDelay: "0.2s" }}></div>
                </div>
              </div>
            </div>
          </div>
        )}
        
        <div ref={messagesEndRef} />
      </div>

      {/* Input Area */}
      <div className="p-4 border-t border-gray-700 bg-gray-800 rounded-b-lg">
        <div className="flex space-x-2">
          <Input
            value={inputValue}
            onChange={(e) => setInputValue(e.target.value)}
            onKeyPress={handleKeyPress}
            placeholder="Type a message to Cyan..."
            className="flex-1 bg-gray-900 border-gray-600 text-white placeholder-gray-400"
            disabled={isTyping}
          />
          <Button
            onClick={sendMessage}
            disabled={!inputValue.trim() || isTyping}
            className="bg-cyan-600 hover:bg-cyan-700 text-white"
          >
            Send
          </Button>
        </div>
        <div className="flex justify-between items-center mt-2 text-xs text-gray-400">
          <span>Press Enter to send</span>
          <span>{messages.length} messages</span>
        </div>
      </div>
    </div>
  );
};

export default ChatInterface;
