"use client";

import React, { useState, useEffect } from "react";

interface VirtualControlsProps {
  isVisible: boolean;
}

const VirtualControls: React.FC<VirtualControlsProps> = ({ isVisible }) => {
  const [mousePosition, setMousePosition] = useState({ x: 100, y: 100 });
  const [isTyping, setIsTyping] = useState(false);
  const [currentText, setCurrentText] = useState("");

  // Simulate Cyan's independent mouse movement
  useEffect(() => {
    if (!isVisible) return;

    const interval = setInterval(() => {
      // Random gentle mouse movements for Cyan
      setMousePosition(prev => ({
        x: Math.max(50, Math.min(window.innerWidth - 50, prev.x + (Math.random() - 0.5) * 20)),
        y: Math.max(50, Math.min(window.innerHeight - 50, prev.y + (Math.random() - 0.5) * 20))
      }));
    }, 2000);

    return () => clearInterval(interval);
  }, [isVisible]);

  // Simulate Cyan's typing behavior
  const simulateTyping = (text: string) => {
    setIsTyping(true);
    setCurrentText("");
    
    let index = 0;
    const typeInterval = setInterval(() => {
      if (index < text.length) {
        setCurrentText(prev => prev + text[index]);
        index++;
      } else {
        clearInterval(typeInterval);
        setTimeout(() => {
          setIsTyping(false);
          setCurrentText("");
        }, 2000);
      }
    }, 100);
  };

  if (!isVisible) return null;

  return (
    <div className="fixed inset-0 pointer-events-none z-50">
      {/* Cyan's Virtual Mouse Cursor */}
      <div
        className="absolute w-6 h-6 transition-all duration-1000 ease-in-out pointer-events-none"
        style={{
          left: mousePosition.x,
          top: mousePosition.y,
          transform: 'translate(-50%, -50%)'
        }}
      >
        <div className="w-full h-full bg-cyan-400 rounded-full shadow-lg animate-pulse">
          <div className="absolute inset-0 bg-cyan-300 rounded-full animate-ping opacity-75"></div>
        </div>
        <div className="absolute -top-8 left-1/2 transform -translate-x-1/2 text-xs text-cyan-300 font-semibold whitespace-nowrap">
          Cyan's Cursor
        </div>
      </div>

      {/* Virtual Keyboard Display */}
      {isTyping && (
        <div className="absolute bottom-20 left-1/2 transform -translate-x-1/2 bg-black/80 border border-cyan-500 rounded-lg p-4 min-w-96">
          <div className="text-cyan-300 text-sm mb-2">Cyan is typing...</div>
          <div className="bg-gray-900 border border-gray-700 rounded p-3 font-mono text-white">
            {currentText}
            <span className="animate-pulse">|</span>
          </div>
        </div>
      )}

      {/* Virtual Keyboard Interface */}
      <div className="absolute bottom-4 left-1/2 transform -translate-x-1/2 bg-black/90 border border-cyan-500 rounded-lg p-2">
        <div className="text-xs text-cyan-300 text-center mb-2">Cyan's Virtual Keyboard</div>
        <div className="grid grid-cols-10 gap-1">
          {/* First row */}
          {['1', '2', '3', '4', '5', '6', '7', '8', '9', '0'].map((key) => (
            <button
              key={key}
              className="w-8 h-8 bg-gray-800 hover:bg-cyan-600 text-white text-xs rounded border border-gray-600 transition-colors"
              onClick={() => simulateTyping(`${key} `)}
            >
              {key}
            </button>
          ))}
          
          {/* Second row */}
          {['Q', 'W', 'E', 'R', 'T', 'Y', 'U', 'I', 'O', 'P'].map((key) => (
            <button
              key={key}
              className="w-8 h-8 bg-gray-800 hover:bg-cyan-600 text-white text-xs rounded border border-gray-600 transition-colors"
              onClick={() => simulateTyping(`${key} `)}
            >
              {key}
            </button>
          ))}
          
          {/* Third row */}
          {['A', 'S', 'D', 'F', 'G', 'H', 'J', 'K', 'L'].map((key) => (
            <button
              key={key}
              className="w-8 h-8 bg-gray-800 hover:bg-cyan-600 text-white text-xs rounded border border-gray-600 transition-colors"
              onClick={() => simulateTyping(`${key} `)}
            >
              {key}
            </button>
          ))}
          <button
            className="w-8 h-8 bg-gray-800 hover:bg-cyan-600 text-white text-xs rounded border border-gray-600 transition-colors"
            onClick={() => simulateTyping("Hello! ")}
          >
            âŽ
          </button>
          
          {/* Fourth row */}
          {['Z', 'X', 'C', 'V', 'B', 'N', 'M'].map((key) => (
            <button
              key={key}
              className="w-8 h-8 bg-gray-800 hover:bg-cyan-600 text-white text-xs rounded border border-gray-600 transition-colors"
              onClick={() => simulateTyping(`${key} `)}
            >
              {key}
            </button>
          ))}
          
          {/* Space bar */}
          <button
            className="col-span-3 h-8 bg-gray-800 hover:bg-cyan-600 text-white text-xs rounded border border-gray-600 transition-colors"
            onClick={() => simulateTyping(" ")}
          >
            SPACE
          </button>
        </div>
      </div>
    </div>
  );
};

export default VirtualControls;
