"use client";

import React, { useState, useEffect } from "react";

const CyanCharacter: React.FC = () => {
  const [expression, setExpression] = useState("neutral");
  const [animation, setAnimation] = useState("idle");

  useEffect(() => {
    const animations = ["idle", "blink", "lookAround", "yawn"];
    let index = 0;
    const interval = setInterval(() => {
      setAnimation(animations[index]);
      index = (index + 1) % animations.length;
    }, 4000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="relative w-48 h-96 select-none">
      <div
        className={`absolute bottom-0 left-1/2 transform -translate-x-1/2 bg-gradient-to-b from-cyan-300 to-cyan-600 rounded-3xl w-40 h-80 shadow-lg transition-all duration-500 ${animation}`}
      >
        <div className="absolute top-0 left-1/2 transform -translate-x-1/2 w-40 h-32 bg-cyan-400 rounded-t-3xl shadow-inner"></div>
        <div className="absolute bottom-0 left-0 w-full h-48 bg-cyan-200 rounded-b-3xl shadow-inner"></div>
        <div className="absolute top-20 left-1/2 transform -translate-x-1/2 w-32 h-32 bg-white rounded-full shadow-md flex flex-col items-center justify-center">
          <div className="flex justify-between w-20">
            <div
              className={`w-6 h-6 bg-blue-500 rounded-full ${
                expression === "blink" ? "opacity-0" : "opacity-100"
              } transition-opacity duration-300`}
            ></div>
            <div
              className={`w-6 h-6 bg-blue-500 rounded-full ${
                expression === "blink" ? "opacity-0" : "opacity-100"
              } transition-opacity duration-300`}
            ></div>
          </div>
          <div className="mt-2 w-12 h-4 bg-pink-400 rounded-full"></div>
        </div>
        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 w-36 h-24 bg-cyan-800 rounded-lg"></div>
      </div>
    </div>
  );
};

export default CyanCharacter;
